import React, { useContext, useState } from 'react'
import './Symptoms.scss'
import pdf from '../../svg/pdf.svg'
import load from '../../svg/load.svg'
import symp1 from '../../svg/symp1.svg'
import symp2 from '../../svg/symp2.svg'
import symp3 from '../../svg/symp3.svg'
import symp4 from '../../svg/symp9.svg'
import symp5 from '../../svg/symp11.svg'
import symp6 from '../../svg/symp5.svg'
import symp7 from '../../svg/symp10.svg'
import symp8 from '../../svg/symp6.svg'
import symp9 from '../../svg/symp7.svg'
import symp10 from '../../svg/symp8.svg'


const Labels = (props) => {
  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        
        <div style={{display:props.loaded?'none':'block'}}  className='preloadblock'></div>
        <img style={{display:props.loaded?'block':'none'}} width='180px' src={props.src} alt="" /></div>
      <div className='label'>{props.name}</div>
    </div>
  )
}

export const Symptoms = (props) => {
  
const [counter, setcounter] = useState(0)
const [loaded, setloaded] = useState(false)
let onLoaded = ()=> {
  setcounter(e=>e+1)
  if (counter==11){
    setloaded(true)
  }


}

  return (
    <div onLoad={onLoaded} ref={props.Ref} className='Symptoms'>
      <div className='title'>
        <div className='left'>
          <h2>  <span>COVID19</span></h2>
          <p>         ,    .</p>

        </div>
        <div className='right'>
          <div style={{ display: 'flex' }}>
            <div><img src={pdf} alt="" /></div>
            <a target='blank' href="https://static-1.rosminzdrav.ru/system/attachments/attaches/000/049/726/original/_A3_koronavirus_for_print.pdf?1584091816">
              <div>
                <div className='name'>  </div>
                <div className='size'>PDF  143 Kb <span> <img src={load} alt="" /></span>
                </div>

              </div>

            </a>

          </div>

          <div className='btn'> </div>
        </div>

      </div>

      <div><h3>  </h3></div>
      <div className='label_wrap'>

        <Labels loaded={loaded} src={symp1} name='  ' />
        <Labels loaded={loaded} src={symp2} name=' (     )' />
        <Labels loaded={loaded} src={symp3} name='' />
        <Labels loaded={loaded} src={symp4} name='  ' />
        <Labels loaded={loaded} src={symp5} name='' />
      </div>


      <div><h3>  </h3></div>
      <div className='label_wrap'>

        <Labels loaded={loaded} src={symp6} name=' ' />
        <Labels loaded={loaded} src={symp7} name='  ' />
        <Labels loaded={loaded} src={symp8} name='' />
        <Labels loaded={loaded} src={symp9} name='' />
        <Labels loaded={loaded} src={symp10} name=', ' />
      </div>
      <div className='up_line'></div>
      <div className='text_under_line'>     <span>14 </span>     .        ,    ,    .</div>
    </div>
  )
}
