import React from 'react'
import './Prevention.scss'
import steps from '../../svg/steps.svg'


const List = (props) => {
  return (
    <div style={{ display: 'flex', position:'relative' }}>
      <div className='list_circle'>{props.number}
     {props.last?'': <div className='linethrough'></div>}
      </div>

      <div className='list_text'>{props.text}</div>
    </div>
  )
}


export const Prevention = (props) => {
  return (
    <div ref={props.Ref} className='prevention_wrapper'>
      <div className='title'>   <h1><span>7 </span>    </h1></div>

      <div className='prevention_main'>
        <div className='left'>

          <div style={{ display: 'flex', position:'relative' }}>
            <div className='list_circle'>1
        <div className='linethrough'></div>
            </div>
            <div className='list_text'>    :  ,    , .</div>
          </div>

          <List number='2' text='    ,   .' />
          <List number='3' text='         ,     (, ,   ).' />
          <List number='4' text='              ,     .' />
          <List number='5' text=' ,   ,   .' />
          <List number='6' text='        .' />
          <List number='7' last={true} text='      (,  ).' />
        </div>
        <div className='right'>
          <img src={steps} alt="" />
        </div>
      </div>
    </div>
  )
}
